import GraphModal from 'graph-modal';
import Swiper from 'swiper';
const catalogIndividual = document.querySelector('.portfolio__individual');
const catalogFamily = document.querySelector('.portfolio__family');
const catalogLovestory = document.querySelector('.portfolio__lovestory');
const catalogWedding = document.querySelector('.portfolio__wedding');
const familyMore = document.querySelector('.portfolio__btn-family');
const lovestoryMore = document.querySelector('.portfolio__btn-lovestory');
const weddingMore = document.querySelector('.portfolio__btn-wedding');
const individualMore = document.querySelector('.portfolio__btn-individual');
const individualModal = document.querySelector('[data-graph-target="individual-modal"] .graph-modal__content');
const familyModal = document.querySelector('[data-graph-target="family-modal"] .graph-modal__content');
const lovestoryModal = document.querySelector('[data-graph-target="lovestory-modal"] .graph-modal__content');
const weddingModal = document.querySelector('[data-graph-target="wedding-modal"] .graph-modal__content');



let modal = null;

let individualQuantity = 12;
let familyQuantity = 12;
let lovestoryQuantity = 12;
let weddingQuantity = 12;

let individualLength = null;
let familyLength = null;
let lovestoryLength = null;
let weddingLength = null;



if (window.innerWidth > 1025) {
    individualQuantity = 4;
    familyQuantity = 4;
    lovestoryQuantity = 4;
    weddingQuantity = 4;
}
if (window.innerWidth > 769 && window.innerWidth < 1025) {
    individualQuantity = 3;
    familyQuantity = 3;
    lovestoryQuantity = 3;
    weddingQuantity = 3;
}
if (window.innerWidth > 0 && window.innerWidth < 769) {
  individualQuantity = 2;
  familyQuantity = 2;
  lovestoryQuantity = 2;
  weddingQuantity = 2;
}


if (catalogIndividual) {
    const loadIndividual = (individualQuantity = 4) => {
      fetch('../individual/individual.json')
        .then((response) => {
          return response.json();
        })
        .then((individual) => {
            individualLength = individual.length;
            
            catalogIndividual.innerHTML = '';
            for (let i = 0; i < individualLength; i++) {
            if (i < individualQuantity) {
                let item = individual[i];
  
                catalogIndividual.innerHTML += `
                <li class="portfolio__item portfolio__individual-item">
                <article class="photo">
                    <button class="btn-reset photo__btn photo__btn-individual" data-graph-path="individual-modal" data-id="${item.id}">
                    </button>
                    <div class="photo__image-individual">
                    <img src="${item.mainImage}" alt="${item.title}">
                    </div>
                    </article>
                </li>
                `
            }
            }


            const modalIndividual = new GraphModal({
              isOpen: (modalIndividual) =>{
                 
                const openBtnId = modalIndividual.previousActiveElement.dataset.id;
                
                
                loadModalIndividual(openBtnId);
              },
          });


        })
    }
  


loadIndividual(individualQuantity);

const loadModalIndividual = (id = 1) =>{
  fetch('../individual/individual.json')
        .then((response) => {
          return response.json();
        })
        .then((individual) => {
          individualLength = individual.length;
          individualModal.innerHTML = '';

          for (let individualItem of individual){
            if (individualItem.id == id){
              individualModal.innerHTML += `
                  <img src="${individualItem.mainImage}" alt="${individualItem.title}">
                `
            }
          }
        })
}

if (individualMore) {

    if (window.innerWidth > 0 && window.innerWidth < 769) {
      individualMore.addEventListener('click', (e) => {
        individualQuantity = individualQuantity + 2;
        loadIndividual(individualQuantity);

        if (individualQuantity >= individualLength) {
          individualMore.style.display = 'none';
        } else {
          individualMore.style.display = 'block';
        }
      })
    }
    if (window.innerWidth > 769 && window.innerWidth < 1025) {
      individualMore.addEventListener('click', (e) => {
        individualQuantity = individualQuantity + 3;
        loadIndividual(individualQuantity);

        if (individualQuantity >= individualLength) {
          individualMore.style.display = 'none';
        } else {
          individualMore.style.display = 'block';
        }
      })
    }
    if (window.innerWidth > 1025) {
      individualMore.addEventListener('click', (e) => {
        individualQuantity = individualQuantity + 4;
        loadIndividual(individualQuantity);

        if (individualQuantity >= individualLength) {
          individualMore.style.display = 'none';
        } else {
          individualMore.style.display = 'block';
        }
      })
    }
  }
}

