document.querySelectorAll(".arrow").forEach(item => {
  item.addEventListener("click", function() {
  let btn = this;
  let dropdown = this.parentElement.querySelector(".sub-menu__list");
    console.log(dropdown);
  document.querySelectorAll(".arrow").forEach(el => {
  if (el != btn) {
      el.classList.remove("arrow--open");
  }
  });
  
  document.querySelectorAll(".sub-menu__list").forEach(el => {
  if (el != dropdown) {
      el.classList.remove("sub-menu__list--open");
  }
  })

  dropdown.classList.toggle("sub-menu__list--open");
  btn.classList.toggle("arrow--open");
})
})

document.addEventListener("click", function(e) {
let target = e.target;
if (!target.closest(".arrow")) {
  document.querySelectorAll(".sub-menu__list").forEach(el => {
      el.classList.remove("sub-menu__list--open");
 
  })
  document.querySelectorAll(".arrow").forEach(el => {
      el.classList.remove("arrow--open");
  });
}
})


